import React from "react";
import "../assets/js/jquery.scrollTo.js";
function Intro() {
  return (
    <div>
      <div id="intro" className="mee">
        <section className="intro wrap">
          <h2 className="hidden">intro</h2>
          <div className="what">
            <p className="text">Lee Kyung Min Portfolio</p>
            <p className="typing">
              {
                <span className="typingB">
                  끊임 없이 연구하는 웹 퍼블리셔 이경민 입니다
                </span>
              }
            </p>
          </div>
          <div className="water-container">
            <p>클릭 하세요!</p>
            <div className="water-wave"></div>
            <a className="swimming arrow-wrap" href="#about_me">
              <img
                src="images/swim.png"
                alt="캐릭터를 클릭하면 아래로 넘어갑니다"
              />
            </a>
            <div className="water-base"></div>
          </div>
        </section>
      </div>
    </div>
  );
}

export default Intro;
